* {
  margin: 0;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  transition: all 0.3s;
}

.header {
  width: 100%;
  height: 60px;
  display: flex;
  align-items: center;
  background-color: #000c66;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 2;
}

.header .header_logo {
  width: 5vw;
  height: 10vh;
  object-fit: contain;
  margin: 0vw 3vw;
  margin-top: -1vh;
}

.header .header_search {
  display: flex;
  flex: 2 1;
  align-items: center;
  border-radius: 24px;
}

.header .header_search .header_searchInput {
  height: 12px;
  padding: 10px;
  border: none;
  width: 80%;
}

.header .header_search .header_searchIcon {
  padding: 5px;
  height: 22px !important;
  background-color: #7ec8e3;
}

.header .header_nav {
  margin-right: 5vw;
  margin-left: 3vw;
}

.header .header_nav .link {
  text-decoration: none;
}

.header .header_nav .link .header_option {
  display: flex;
  flex-direction: column;
  margin-left: 2vw;
  margin-right: 2vw;
  color: white;
}

.header .header_nav .link .header_optionBasket {
  display: flex;
  align-items: center;
  color: white;
}

.header .header_nav .link .header_optionBasket .acceseaza {
  text-align: center;
  width: 50%;
  font-size: 15px;
}

.header .header_nav .link .header_optionBasket .cosIcon {
  transition: all 0.3s;
}

.header .header_nav .link .header_optionBasket .cosIcon:hover {
  transform: scale(1.3);
}

.header .header_nav .link .header_optionBasket .header_basketCount {
  margin-left: 0.5vw;
  margin-right: 2vw;
}

.header .header_optionLineOne {
  font-size: 10px;
}

.header .header_optionLineTwo {
  font-size: 13px;
  font-weight: 800;
}

@media screen and (max-width: 700px) {
  .header_logo {
    width: 9vw !important;
    margin-top: 0 !important;
  }
  .header_searchInput {
    width: 26.8vw !important;
  }
  .acceseaza {
    font-size: 12px !important;
  }
  .cosIcon {
    margin-top: 1vh;
  }
  .header_optionBasket {
    justify-content: space-between;
  }
}

@media screen and (max-width: 1026px) {
  .header_logo {
    margin-top: 0.5vh !important;
  }
}

@media screen and (max-width: 300px) {
  .header_logo {
    width: 12vw !important;
  }
  .acceseaza {
    font-size: 11px !important;
  }
}
