.home {
  display: flex;
  justify-content: center;
  max-width: 1500px;
}

.home .home_container .home_image {
  width: 100%;
  z-index: -1;
  -webkit-mask-image: linear-gradient(to bottom, black, rgba(0, 0, 0, 0));
          mask-image: linear-gradient(to bottom, black, rgba(0, 0, 0, 0));
}

.home .home_container .home_row {
  display: flex;
  z-index: 1;
  margin-left: 5px;
  margin-right: 5px;
  justify-content: center;
}

@media screen and (max-width: 750px) {
  .home_container {
    margin-top: -3.3vh;
  }
  .home_image {
    margin-top: 3vh;
  }
}
/*# sourceMappingURL=Home.css.map */
.product {
  display: flex;
  flex-direction: column;
  background-color: white;
  z-index: 1;
  align-items: center;
  justify-content: flex-end;
  margin: 20px;
  margin-top: 10px;
  padding: 0px 5px 0px 5px;
  width: 80%;
}

.product .product_info {
  margin-bottom: 15px;
}

.product .product_info p {
  font-weight: 500 !important;
}

.product .product_info .product_price {
  margin-top: 5px;
}

.product .product_rating {
  display: flex;
}

.product img {
  max-height: 250px;
  width: 100%;
  object-fit: contain;
  margin-bottom: 15px;
}

.product button {
  background: #7ec8e3;
  border: 1px solid;
  margin-top: 5px;
  border-color: #68a7be;
  color: #111;
  border-radius: 2px;
  cursor: pointer;
}
/*# sourceMappingURL=Product.css.map */
.Products {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  margin-left: 5vh;
}
@media screen and (max-width: 750px) {
  .Products {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
    margin-top: 0vh;
  }
}
@media screen and (min-width: 1000px) {
  .Products {
    margin-top: -10vh;
  }
}

.checkout {
  display: flex;
  padding: 20px;
  background-color: white;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
}

.checkout .checkout_left .checkout_ad {
  width: 100%;
  margin-bottom: 10px;
}

.checkout .checkout_left .checkout_title {
  margin-right: 10px;
  pad: 10px;
  border-bottom: 1px solid lightgray;
}

@media screen and (max-width: 600px) {
  .checkout {
    flex-direction: column-reverse;
  }
  .checkout_ad {
    margin-left: -5vw;
    margin-right: 0;
    width: 99vw !important;
    height: 5vh;
  }
}
/*# sourceMappingURL=Checkout.css.map */
.checkoutProduct {
  display: flex;
  margin-top: 20px;
  margin-bottom: 20px;
}

.checkoutProduct .checkoutProduct_image {
  object-fit: contain;
  width: 180px;
  height: 180px;
}

.checkoutProduct .checkoutProduct_info {
  padding-left: 20px;
}

.checkoutProduct .checkoutProduct_info button {
  background: #7ec8e3;
  border: 1px solid;
  margin-top: 10px;
  border-color: #68a7be;
  color: #111;
}

.checkoutProduct .checkoutProduct_info .checkoutProduct_rating {
  display: flex;
}

.checkoutProduct .checkoutProduct_info .checkoutProduct_title {
  font-size: 17px;
  font-weight: 500;
}

.checkoutProduct .checkoutProduct_info .checkoutProduct_price {
  margin-top: 0.5vh;
}
